/* NavBar Style */
.App1{
  display: none;
}
.MHome{
  background: #ffffff;
}

.Home button{
  display: none;
}
.iconmnu .fa.fa-bars{
  color: #ffffff;
  background: #000;
  display: none;
}


@media screen and (max-width:400px ){
  .App .Footer h3{
      font-size: 15px;
  }
  .App .Footer h4{
    font-size:10px ;
  }
  .App .Home .Button{
    display: block;
  }
 
  
}
@media screen and (max-width:600px){
  .iconmnu .fa.fa-bars{
    color: #ffffff;
    background: #000;
    display: block;
  }
  .App .Home nav{
    flex-direction: column;
  }
  
  .App .Home nav ul li:first-child{
    display: none;
  }
  .App .Home nav ul li:not(:nth-child(2)){
    display: none;
  }
  .App .Home .nav .ul .li1.R {
    display: block;
  }
  .App .Home .nav .ul .li2.R {
    display: block;
  }
  .App .Home .nav .ul .li3.R {
    display: block;
  }
  .App .Home .nav .ul .li4.R {
    display: block;
  }
  
  .App .Home nav ul li{
    width: 200px;
    border:1px solid  #ffffff;
  }
  .App .Contact .ltr1{
    color: #f4f4f4;
  }
  .App .Contact .Form{
    margin: 0;
    background: #e6e6e6;
    padding: 0;
  }
  .App .Contact .Form textarea{
    width: 250px;
    background: #ffffff;

  }
  .App .Contact .Form input{
    width:250px;
    background: #ffffff;
  }
.App .Contact .Form form{
  margin-top: 0;
  margin-left: 10px;
}
  .App .Home nav ul li{
    padding: 10px;
    
  }
  .App .Home nav ul{
    display: flex;
    flex-direction: column;
  }
  .App .About .About-body .About-content h2{
      font-size: 15px;
  }
  .App .About .About-body{
    width: 200px;
    margin-top: 0;
  }
  .App1{
    /* display: block; */
    margin: 0;
    width: 100%;
    height: 100vh;
    background-size: cover;
    background: #b30000;

  }
  .App .Products{
    margin: 10px;
    padding: 10px;
  }
  .App .Home{
    background: #840000;
    margin: 0;
    padding: 0;
    background-size:cover;
    width: 100%;
    height:100vh;
  }
  .App1 .Products{
    background: #b71111;
  }
  .App1 .About{
    background: #e6e6e6;
  }
  .App1 .Contact{
    background: #b71111;
  }
  .App1 .Footer{
    background: #191313;
  }
  .App1 .Home nav{
     background: #000;
  }
}
nav{
  background-color: rgba(127, 255, 212, 0);
}
nav ul{
 
  display: flex;
  margin: 0;
 
}
nav ul li:not(:first-child){
  position: relative;
  margin-top: 20px;
  float: right;
}
nav ul li a{
  text-decoration: none;
  color: #000;
}
nav ul li a:hover{
  color: rgb(216, 26, 26);
  font-weight: 800;
}
nav ul li{
  list-style: none;
  padding: 30px;
  font-size: 15px;
  font-weight: 700;
  
}

nav ul li:not(:first-child):hover{
  color: rgb(216, 26, 26);
  font-weight: 700;
}
.icon{
  background:url("./Images/icon.png");
  background-repeat: no-repeat;
  background-size: 50px;


}
.titl{
  color: rgb(155, 0, 0);
  font-weight: 900;
  font-size: 35px;
  display: flex;
  margin-right: 200px;
 
}
/* Home Style */
.Home{
  background: url("./Images/background6.png");
 
  margin: 0;
  padding: 0;
  background-size:cover;
  width: 100%;
  height:100vh;
}
/* Products Style */
.Products{
  margin: 80px;
  text-align: center;
}
.Products h1{
  font-size: 35px;
  
}
.cards{
  display: flex;
  
}
.Scroll{
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  height: 500px;
  margin-top: 80px;
  width: 100%;
  padding: 10px;

}
 .Scroll::-webkit-scrollbar-thumb {
  background: red; 
  border-radius: 10px;
}
.Scroll::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius:10px;
}

.ltr1{
  color: #b71111;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
/* Card style */
::-webkit-scrollbar {
  width: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 0px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #b71111; 
  border-radius: 0px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
}
.card{
    margin-right: 100px;
    width:330px;
    height: 450px;
    
    border-radius: 5px;
    box-shadow:  -1px 10px 15px 0px rgb(0, 0, 0);
}
.card:hover{
  border: none;
  border-bottom: 3px solid red;
  box-shadow:  -1px 15px 15px 0px rgb(0, 0, 0);

}
.img{
   background-repeat: no-repeat;
   background-size: cover;
   width:330px;
   margin: 0;
   height: 250px;
  
}
.Button{
  
  background-color: #000;
  font-size: 20px;
  padding: 10px 25px;
  margin-top: 10px;
  border: none;
  font-family: Georgia, 'Times New Roman', Times, serif;
  width: 160px;
  color: aliceblue;
 
 
}
.Button:hover{
  background-color: rgb(248, 23, 23);
}
.Para{
  padding: 15px;
  text-align: left;
  font-size: 14px;
}

/* About Style */
.About{
  text-align: center;
  background-image: url("./Images/About.png");
  margin: 0;
  padding: 0;
  background-size:cover;
  width: 100%;
  height:100vh;
}
.About h1{
  font-size: 35px;
}
.About-body{
  display: flex;
  margin: 80px;
  width: 600px;
  text-align: left;

}
.About-content.R{
  animation-name: para-slid;
  animation-duration: 2s;
  animation-iteration-count: 1;
}
@keyframes para-slid{
  0%{
    transform: translateX(300px);
  }
  100%{ 
    transform: translateX(0px);
  }
}
/* Contact */
.Contact{
  background-image: url("./Images/Contact2.png");
  margin: 0;
  padding: 0;
  background-size:cover;
  width: 100%;
  height:100vh;
  text-align: center;
}
.Form{
 padding: 10px;
 margin-left: 170px;
  text-align: left;
  width: 100;
  height:100vh;
}
form{
  display: flex;
  flex-direction: column;
  margin-top: 100px;
 
  
}
.anim-class.R{
  animation-name: form-slid;
  animation-duration: 2s;
  animation-iteration-count: 1;

}
  @keyframes form-slid{
    0%{transform:translateX(-100px)}
    100%{transform:translateX(0px)}
  }


.Form textarea{
  background-color: rgb(238, 238, 238);
  width: 300px;
  font-size: 20px;
  height: 100px;
  border:2px solid #000;
  margin-top: 10px;
}
.Form textarea::placeholder{
  font-size: 16px;
  color: #000;
 
}
.Form input:hover{
  border-bottom: 2px solid rgb(155, 0, 0);
}
.Form label{
  font-size: 20px;
  margin-top: 40px;
  font-weight: bold;
}
.Form input::placeholder{
  color: #000;
  font-size: medium;
}
.Form input{
  outline: none;
  margin-top: 40px;
  background-color: rgb(238, 238, 238);
  width: 300px;
  font-size: 20px;
  border: none;
  border-bottom: 2px solid #000;
}
/* Footer style */
.Footer{
  margin: 0;
  padding: 0;
  background-color: #191313;
  background-size: cover;
  width: 100%;
  height: 50vh;
  text-align: center;
}
.Footer h3{
  color: antiquewhite;
  padding: 30px;
}
.Footer-icon{
  display: flex;
  justify-content: center;
  
}
.fa{
  padding: 10px;
  text-decoration: none;
  color: #b30000;
  
}
.Footer-icon a{
  font-size: 30px;
}
.Footer-icon a:hover{
  color:antiquewhite;
}
.Footer h4{
  color: antiquewhite;
}